import { Field, Formik } from 'formik'
import React from 'react'
import { useSelector } from 'react-redux'
import { FilterType } from '../../redux/usersReducer'
import { getFilter } from '../../redux/usersSelector'

const usersSearchFormValidate = (values: FormProps) => {
	const errors = {} as any
	if (!values) {
		errors.term = 'Required'
	}
	return errors
}

export const UsersSearchForm: React.FC<Props> = props => {
	const submit = (
		values: FormProps,
		{ setSubmitting }: { setSubmitting: (isSubmitting: boolean) => void }
	) => {
		const IFilter: FilterType = {
			term: values.term,
			friend:
				values.friend === 'null'
					? null
					: values.friend === 'true'
					? true
					: false,
		}
		props.onFilterChange(IFilter)
		setSubmitting(false)
	}
	const filter = useSelector(getFilter)
	return (
		<>
			<Formik
				enableReinitialize={true}
				initialValues={{
					term: filter.term,
					friend: String(filter.friend) as FriendFormType,
				}}
				validate={usersSearchFormValidate}
				onSubmit={submit}
			>
				{({
					values,
					errors,
					touched,
					handleChange,
					handleBlur,
					handleSubmit,
					isSubmitting /* and other goodies */,
				}) => (
					<form onSubmit={handleSubmit} className='ml-2'>
						<input
							className='rounded-l-lg'
							type='text'
							name='term'
							onChange={handleChange}
							onBlur={handleBlur}
							value={values.term}
						/>
						{errors.term && touched.term && errors.term}
						<Field className='rounded-r-lg' as='select' name='friend'>
							<option value='null'>all</option>
							<option value='true'>only followed</option>
							<option value='false'>only unFollowed</option>
						</Field>
						<button
							className='m-3 px-10 py-2 
								rounded bg-orange-300 
								hover:bg-orange-400 active:bg-orange-500
								transition-all '
							type='submit'
							disabled={isSubmitting}
						>
							Search
						</button>
					</form>
				)}
			</Formik>
		</>
	)
}
type Props = {
	onFilterChange: (filter: FilterType) => void
}
type FriendFormType = 'null' | 'true' | 'false'

type FormProps = {
	term: string
	friend: FriendFormType
}
